@import '/src/styles/vars.scss';

.btn-connect-wallet {
  background: $btn-bg;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 14vw;
  height: 8vh;
  transition: transform 0.3s;
}

.btn-connect-wallet:hover {
  display: flex;
  justify-content: center;
  align-items: center;
  color: $white;
  font-family: avenFont;
  font-size: 2rem;
  font-weight: 500;
  background: linear-gradient(270deg, #9e00ff -15.06%, #25caee 55.25%, #1944e0 111.63%);
  border-radius: 3px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 10%;
  width: 14vw;
  height: 8vh;
  transform: scale(110%);
  transition: transform 0.3s;
}
