@import '/src/styles/vars.scss';

.greeting {
  display: flex;
  flex-direction: column;
}

.greeting__h1 {
  color: $white;
  font-family: h1;
  font-size: 7rem;
  text-transform: uppercase;
  line-height: 8rem;
  position: relative;
}

.h1__neon {
  position: absolute;
  margin-left: 25px;
}

.greeting__h2 {
  margin-top: 3vh;
  font-family: bodyFont;
  font-size: 2.8rem;
  color: $white;
  line-height: 4rem;
}

.greeting__btn {
  margin-top: 5vh;
  height: 10vh;
  width: 16vw;
  box-shadow: $neon-pink;
  transition: box-shadow 0.3s;
}

.greeting__btn:hover {
  box-shadow: none;
  transition: box-shadow 0.3s;
}
