@import '/src/styles/vars.scss';

.metacombine {
  display: flex;
  flex-direction: column;
  margin-left: 10vw;
  color: $white;
}

.metacombine__h1 {
  font-family: h1;
  font-size: 7rem;
  letter-spacing: 2%;
}

.metacombine__h2 {
  font-family: avenFont;
  font-weight: lighter;
  font-size: 7rem;
  margin-top: 2vh;
}

.metacombine__h3 {
  font-size: 2.8rem;
  margin-top: 5vh;
  line-height: 4rem;
}

.metacombine__btn {
  margin-top: 5vh;
  width: 269px;
  height: 80px;
}
