@import '/src/styles/vars.scss';

.section5-wrapper {
  background-image: url(~/src/images/bg5.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  height: 250vh;
  position: relative;
}

.section5-wrapper__team-members {
  display: flex;
  flex-direction: column;
}

.team-members__h1 {
  align-self: center;
  color: $white;
  font-family: avenFont;
  font-size: 7rem;
  margin: 20vh auto 7vh auto;
}

.team-members__members-slider {
  display: flex;
  margin: 0 auto 30vh auto;
  align-items: center;
}

.members-slider__left {
  display: flex;
  background-image: url(~/src/images/team-members-left.svg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  height: 6vh;
  width: 8vh;
  cursor: pointer;
}

.members-slider__right {
  display: flex;
  background-image: url(~/src/images/team-members-right.svg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  height: 6vh;
  width: 8vh;
  cursor: pointer;
}

.members-slider__member-items {
  display: flex;
}

.member-items__item {
  display: flex;
  position: relative;
  align-items: center;
}

.item__member-image {
  background-size: cover;
  background-position: center center;
  position: absolute;
  height: 38vh;
  width: 28vh;
  top: 0;
  left: 10%;
}

.item__member-image-select {
  background-size: cover;
  background-position: center center;
  position: absolute;
  height: 48vh;
  width: 38vh;
  top: 0;
  left: 10%;
}

.item__border-image {
  display: flex;
  background-image: url(~/src/images/border-image.png);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  height: 40vh;
  width: 30vh;
}

.item__border-image-select {
  display: flex;
  background-image: url(~/src/images/border-image-select.png);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  height: 50vh;
  width: 40vh;
}

.team-members__h3 {
  color: $white;
  font-size: 2.8rem;
  align-self: center;
  margin-bottom: 5vh;
}

.footer {
  display: flex;
  flex-direction: column;
  background: rgba(255, 255, 255, 0.1);
}

.wrapper__socials {
  display: flex;
  justify-content: center;
  margin-top: 7vh;
  gap: 3vw;
}

.socials__img {
  align-self: center;
  margin-right: 3vw;
  &:last-child {
    margin-right: 0;
  }
}

.wrapper__footer-nav {
  color: $white;
  display: flex;
  justify-content: center;
  margin: 10vh auto;
}

.footer-nav__item {
  display: flex;
  flex-direction: column;
  margin-right: 5vw;
}

.item__h4 {
  font-family: avenFont;
  font-size: 2.3rem;
  margin-bottom: 5vh;
}

.item__footer-link {
  margin-bottom: 1vh;
  font-size: 2.1rem;
}
