@import '/src/styles/vars.scss';

.section4-wrapper {
  background-image: url(../images/bg4.jpg);
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  height: 250vh;
  position: relative;
}

.section4-wrapper__content {
  display: flex;
  flex-direction: column;
}

.content__h2 {
  display: flex;
  justify-content: center;
  font-size: 2.8rem;
  margin-top: 2vh;
}

.h2__text {
  padding: 2vh;
}

.content__text {
  display: flex;
  justify-content: center;
  text-align: center;
  color: $white;
  font-size: 2.1rem;
  margin-top: 7vh;
  line-height: 35px;
}

.content__wrapper-telegram {
  display: flex;
  flex-direction: column;
}

.content__telegram-row {
  display: flex;
  justify-content: center;
  position: relative;
  margin: 10vh 15vw 5vh 15vw;
}

.content__telegram-row2 {
  display: flex;
  justify-content: center;
  position: relative;
  margin: 3vh 0 5vh 15vw;
}

.content__telegram-row3 {
  display: flex;
  justify-content: center;
  position: relative;
  margin: 3vh 15vw 5vh 0;
}

.telegram-row__icon {
  display: flex;
  position: absolute;
  left: 0;
  bottom: 0;
  z-index: 3;
}

.telegram-row__row-items {
  display: flex;
  padding: 10vh 20vw;
  margin: 5vh;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  z-index: 2;
}

.telegram-row__row-items2 {
  display: flex;
  padding: 10vh 4vw;
  margin: 5vh;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  z-index: 2;
}

.glass-1 {
  backdrop-filter: blur(15px) saturate(180%);
  -webkit-backdrop-filter: blur(15px) saturate(180%);
  background-color: rgba(242, 242, 242, 0.1);
  border-radius: 25px;
  border: 3px solid rgba(81, 101, 246, 0.7);
}

.glass-2 {
  backdrop-filter: blur(15px) saturate(180%);
  -webkit-backdrop-filter: blur(15px) saturate(180%);
  background-color: rgba(242, 242, 242, 0.1);
  border-radius: 25px;
  border: 3px solid rgba(37, 197, 239, 0.7);
}

.glass-3 {
  backdrop-filter: blur(15px) saturate(180%);
  -webkit-backdrop-filter: blur(15px) saturate(180%);
  background-color: rgba(242, 242, 242, 0.1);
  border-radius: 25px;
  border: 3px solid rgba(241, 85, 165, 0.7);
}

.row-items__item-h3 {
  font-size: 4rem;
  font-family: avenFont;
  color: $white;
  margin-bottom: 5vh;
}

.row-items__item-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  background: $white;
  font-family: avenFont;
  font-size: 2rem;
  font-weight: 500;
  width: 12vw;
  height: 10vh;
  color: $blue;
}

.content__telegram-wrapper-row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.partners-wrapper__h2 {
  margin-top: 20vh;
  margin-left: 10vw;
  display: flex;
  justify-content: center;
  font-family: avenFont;
  font-size: 7rem;
  color: $white;
  align-items: baseline;
}

span.gradient {
  padding: 2vh;
}

.content__partners-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.partners-wrapper__glass-slider-mini {
  width: 60vw;
  padding: 5vh;
  margin-top: 10vh;
  margin-left: 10vw;
}

.glass-slider-mini__h3 {
  font-family: h1;
  font-size: 3.5rem;
  color: $white;
  display: flex;
  justify-content: center;
  margin-bottom: 5vh;
}
