@import '/src/styles/vars.scss';

.section1-wrapper {
  background-image: url(~/src/images/bg1.jpg);
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  background-repeat: no-repeat;
  height: 100vh;
  position: relative;
  @include media('max', 'lg') {
    height: 125vh;
  }
}

.greeting__img {
  background-image: url(~/src/images/charter1.png);
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  width: 30vw;
  display: flex;
  align-items: flex-end;
  position: absolute;
  z-index: 2;
  transition: transform 0.3s;
  bottom: 0;
  @include media('max', 'xxxl') {
    width: 40vw;
  }
  @include media('max', 'xxlg') {
    width: 45vw;
    height: 95vh;
  }
  @include media('max', 'xlg') {
    width: 35vw;
    height: 75vh;
  }
  @include media('max', 'lg') {
    width: 33vw;
    height: 39vh;
  }
}

.greeting__img:hover {
  transform: scale(110%);
  transition: transform 0.3s;
}

.section-wrapper__buble {
  z-index: 1;
  height: 100vh;
  position: absolute;
  left: -22vh;
  top: -9vh;
  transition: transform 0.3s;
  @include media('max', 'xxxl') {
    height: 83vh;
    left: -19vh;
    top: -5vh;
  }
  @include media('max', 'xlg') {
    height: 83vh;
    left: -16vh;
    top: -6vh;
  }
}

.section-wrapper__buble:hover {
  transform: scale(110%);
  transition: transform 0.3s;
}

.section1-wrapper__container {
  display: flex;
  z-index: 5;
}

.section-wrapper__greeting {
  z-index: 3;
  display: flex;
  justify-content: flex-end;
  margin: 25vh 12vw 0 0;

  @include media('max', 'xxlg') {
    margin: 10vh 3vw 0 0;
  }
}

header {
  z-index: 5;
}
