@import '/src/styles/vars.scss';

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  color: $white;
  font-family: avenFont;
  font-size: 2rem;
  font-weight: 500;
  background: linear-gradient(270deg, #9e00ff -15.06%, #25caee 55.25%, #1944e0 111.63%);
  border-radius: 3px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 10%;
  transition: box-shadow 0.3s;
  box-shadow: none;
}

.btn:hover {
  box-shadow: $neon-blue;
  transition: box-shadow 0.3s transform 0.3s;
  transform: scale(110%);
}
