@import '/src/styles/vars.scss';

.glass-slider {
  display: flex;
  flex-direction: column;
  margin: 0 7vw 15vh 7vw;
  padding: 10vh 6vw 0 6vw;
}

.glass-slider__header-slider {
  display: flex;
  justify-content: space-between;
  color: $white;
  align-items: center;
  margin-bottom: 7vh;
}

.header-slider__h1 {
  font-family: avenFont;
  font-size: 7rem;
  font-weight: lighter;
}

.header-slider__nav-slider {
  display: flex;
  justify-content: space-between;
}

.nav-slider__left {
  margin-right: 3vw;
  cursor: pointer;
}

.nav-slider__left:hover .glass-slider-left {
  stroke: $glass-nav-active;
}

.glass-slider-left {
  display: flex;
  &:active {
    stroke: $glass-nav-active;
  }
}

.nav-slider__right {
  cursor: pointer;
}

.nav-slider__right:hover .glass-slider-right {
  stroke: $glass-nav-active;
}

.glass-slider-right {
  display: flex;
  &:active {
    stroke: $glass-nav-active;
  }
}

.glass-slider__content-slider {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  overflow: hidden;
}

.content-slider__glass-slider {
  display: flex;
}

.content-slider__item-slider {
  display: flex;
  flex-direction: column;

  $numBoxes: 7;
  $step: 360 / $numBoxes;

  &:first-child {
    margin-left: 0;
  }

  @for $i from 1 through $numBoxes {
    &:nth-child(#{$i}) {
      z-index: $numBoxes - $i + 1;
    }
  }
}

.item-slider__image {
  display: flex;
  position: relative;
  margin-bottom: 2vh;
}

.image__slider-btn {
  display: flex;
  position: absolute;
  bottom: 2vh;
  right: 1vw;
  color: $white;
  background: rgba(19, 19, 19, 0.8);
  border-radius: 5px;
  padding: 1vh 1vw;
  font-size: 1.8rem;
  text-transform: uppercase;
}

.image__img {
  filter: drop-shadow(0px 4px 5px rgba(0, 0, 0, 0.25));
  border-radius: 21px;
}

.item-slider__h4 {
  color: $white;
  font-family: avenFont;
  font-size: 2.3rem;
  margin-bottom: 1vh;
}

.item-slider__name {
  color: $white;
  text-transform: uppercase;
  margin-bottom: 2vh;
  font-size: 2.1rem;
}

.item-slider__website-link {
  color: $glass-link;
  font-size: 2.1rem;
  text-transform: uppercase;
  margin-bottom: 2vh;
}
