@import '/src/styles/vars.scss';

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  margin: 6vh 6vw 0 6vw;
  font-family: avenFont;
  font-weight: 500;
  z-index: 4;
  @include media('max', 'xxl') {
    margin: 3vh 3vw 0 3vw;
  }
}

.menu__list {
  display: flex;
  align-items: inherit;
  justify-content: inherit;
  text-transform: uppercase;
  color: $white;
  font-size: 2rem;
}

.list__item {
  margin-right: 3.5vw;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.2);
  &:last-child {
    margin-right: 0;
  }
}
