//
// Colors
//

$bg: #100f13; // Background color if the image doesn't load
$white: #fff; // White color
$blue: #5165f6; // Blue color

$glass-link: #25caee; // Color for glass slider links
$glass-nav-active: #f054a5; // Color for glass slider navigation

$btn-bg: linear-gradient(270deg, #161616 43.41%, #0b0b25 111.63%);

//Pink neon effect

$neon-pink: 0 0 1px #ffffff, 0 0 3px #ffffff, 0 0 7px #ffffff, 0 0 10px rgba(240, 84, 165, 0.5),
  0 0 12px rgba(240, 84, 165, 0.4), 0 0 15px rgba(240, 84, 165, 0.3), 0 0 17px rgba(240, 84, 165, 0.2),
  0 0 20px rgba(240, 84, 165, 0.1);

//Blue neon effect

$neon-blue: 0 0 1px #ffffff, 0 0 3px #ffffff, 0 0 7px #ffffff, 0 0 10px rgba(37, 197, 239, 0.5),
  0 0 12px rgba(37, 197, 239, 0.4), 0 0 15px rgba(37, 197, 239, 0.3), 0 0 17px rgba(37, 197, 239, 0.2),
  0 0 20px rgba(37, 197, 239, 0.1);

//
//Mixins for @media
//

$sizes: (
  'xs': 360px,
  'sm': 576px,
  'md': 768px,
  'lg': 1024px,
  'xlg': 1280px,
  'xxlg': 1366px,
  //1366x768
  'xl': 1440px,
  //1440x900
  'xxl': 1536px,
  //1536x864
  'xxxl': 1680px,
  //1680x1050
);

/*
Example

@include media("max", "xxl") {
  .div {
    width: 100px;
  }
}

An example of a request if you need to specify the boundaries of a specific range!

@include media("within", "md") {
  .div {
    width: 100px;
  }
}
*/

@mixin media($minmax, $media) {
  @each $size, $resolution in $sizes {
    @if $media == $size {
      @if ($minmax != 'within') {
        @media only screen and (#{$minmax}-width: $resolution) {
          @content;
        }
      } @else {
        @if (index(map-keys($sizes), $media) > 1) {
          @media only screen and (min-width: getPreviousSize(($media)+1)) and (max-width: $resolution) {
            @content;
          }
        } @else {
          @media only screen and (max-width: $resolution) {
            @content;
          }
        }
      }
    }
  }
}
