@import '/src/styles/vars.scss';

.content__benefits-row {
  display: flex;
  justify-content: space-evenly;
}

.benefits-row__content-items {
  display: flex;
  flex-direction: column;
  margin-bottom: 5vh;
  &:nth-child(1) {
    margin-top: 15vh;
  }
  &:nth-child(2) {
    margin-top: 15vh;
  }
}
.content-items__h3 {
  font-family: avenFont;
  font-size: 2.8rem;
  color: $white;
}

.content-items__number {
  font-family: h1;
  font-size: 5rem;
  margin: 3vh 0;
}

.content-items__text {
  font-size: 2.1rem;
  color: $white;
  line-height: 4rem;
}
